.jumbotron{
    background:  linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5)),     url("./images/jumbotron.jpg") no-repeat center center !important; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    color: white;
      
}

.btn-primary, .badge-primary{
    background-color: #006bf8;
}

.btn-primary:hover{
    color: #fff;
    background-image: none;
    border-color: #007bff;
}
.btn-primary:hover::after, .btn-success:hover::after{
    font-family: "Font Awesome 5 Free";
    content: "\f061";
    display: inline-block;
    padding-left: 4px;
    font-weight: 700;

}

.btn-success:hover{
    color: #fff;
    /* background-color: transparent; */
    background-image: none;
    border-color: #28a745;
}



.text-white {
    color: #ffffff;
}

.bg-danger {
    background-color: #9B0F1D !important;
}

