.tooltiptext {
    visibility: none;
    width: 120px;
    background-color: #9B0F1D;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px 0;
    
    /* Position the tooltip */
    position: absolute;
    z-index: 99;
    bottom: 100%;
    opacity: 0.7;
    
  }
  
  .tooltiptext::after {
      content: " ";
      position: absolute;
      top: 100%; /* At the bottom of the tooltip */
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: black transparent transparent transparent;
    }

    .footer {
        width: 100%;
        height: 100px;
        border-top: 1px solid #eaeaea;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      
      .logo {
        height: 1em;
        margin: 5px;
      }
      a:hover{
        color: #9B0F1D !important;
        text-decoration: none;
      }